<script setup>
import BaseHeader from '@/components/common/header/baseHeader.vue'
import BaseFooter from '@/components/common/footer/baseFooter.vue'
import { useStore } from "vuex";
import axios from "axios";
import { ref} from 'vue';

import { AgGridVue } from 'ag-grid-vue3'; // Vue3 AgGrid Component

import 'ag-grid-community/styles/ag-grid.css' //   css
import 'ag-grid-community/styles/ag-theme-alpine.css' //  

  const store = useStore();
  const rowData = ref([]);

  const getBoardList = async () => {
    store.commit("changeStatus", true);
    await axios.post("/boardList.do",{})
    .then((res) => {
      console.log('',res.data.RESULT_LIST);
      rowData.value = res.data.RESULT_LIST;
      return res.data.RESULT_LIST;
    })
    .catch((res) => {
      console.error("", res);
      rowData.value = {};
      return 'ERROR'
    });
    store.commit("changeStatus", false);
  }

  const colDefs = ref([
    { field: "CATEGORY",headerName: '', checkboxSelection: true },
    { field: "CODE_NAME", headerName: '',},
    { field: "TITLE" ,headerName: '',editable: true, },
    { field: "BODY_TEXT", headerName: '', },
    { field: "REG_DT2",headerName: '',editable: true, cellEditor: 'agDateStringCellEditor',
        cellEditorParams: {
            min: '2020-01-01',
            max: '2027-12-31',
        } }
  ]);

  let gridObject = {};
  const onMountGrid =(prop) =>{
    console.log(prop);
    gridObject = prop.api;
  }

  const saveBoardData =() =>{

    gridObject.startEditingCell({
    rowIndex: 0,
    colKey: 'TITLE'
});

    console.log(gridObject.getSelectedNodes());
  }

</script>

<template>
<div>
  <BaseHeader title=""></BaseHeader>
  <main>
    <div>
       
    </div>
    <div>
      <button type="button" id="searchBtn" @click="getBoardList"></button>
      <button type="button" id="searchBtn" @click="saveBoardData"></button><br>
    </div>
    <div>
      <ag-grid-vue 
        style="height: 480px" 
        class="ag-theme-alpine" 
        :rowData="rowData" 
        :columnDefs="colDefs" 
        rowSelection="multiple"
        singleClickEdit = true 
        @grid-ready="onMountGrid"
      >
      </ag-grid-vue>
    </div>
  </main>
  <BaseFooter></BaseFooter>
</div>
</template>

